header {
  background-color: var(--text-clr);
  text-align: center;
  position: fixed;
  width: 100%;
  z-index: 999;
  height: 10vh;
  font-size: 1em;
  display: -webkit-flex;
  display: flex;
  top: 0;
}

header h1 {
  padding: unset;
  font-size: 2em;
  -webkit-justify-content: center;
          justify-content: center;
  margin: auto;
  text-transform: capitalize;
  color: var(--foreground-clr)
}
.nav-toggle {
  display: none;
  position: relative;
}

.nav-toggle-label {
  position: absolute;
  top: 0;
  left: 0;
  margin-left: 1em;
  height: 100%;
  display:-webkit-flex;
  display:flex; 
  -webkit-align-items: center; 
          align-items: center;
}

.logo a {
  text-decoration: none;
  color: var(--dark-accent-clr);
}
.nav-toggle-label::before {
  content: "";
  display: inline-block;
  position: relative;
  width: 2.2em;
  bottom: 12px;
  left: 2.2em;
  -webkit-transform: scale(0,1);
          transform: scale(0,1);
  -webkit-transition: -webkit-transform 200ms ease-in-out;
  transition: -webkit-transform 200ms ease-in-out;
  transition: transform 200ms ease-in-out;
  transition: transform 200ms ease-in-out, -webkit-transform 200ms ease-in-out;
}
.nav-toggle-label:hover::before {
  -webkit-transform: scale(1,1);
          transform: scale(1,1)
}
.nav-toggle:focus ~ .nav-toggle-label::before{
  -webkit-transform: scale(1,1);
          transform: scale(1,1)
}

.nav-toggle-label span,
.nav-toggle-label span::before,
.nav-toggle-label span::after {
  all: unset;
  display: block;
  background: var(--dark-accent-clr);
  height: .2em; 
  width: 2em; 
  border-radius: .2em;
  position: relative;
}

.nav-toggle-label span::before,
.nav-toggle-label span::after{
  content: "";
  position: absolute;
}

.nav-toggle-label span::before {
  bottom: 7px;
}
.nav-toggle-label span::after {
  top: 7px;
}

nav {
  position: absolute; 
  text-align: left;
  background: var(--text-clr);
  top: 100%;
  width: 100%;
  left: 0;
  -webkit-transform: scale(1, 0);
          transform: scale(1, 0);
  -webkit-transform-origin: top;
          transform-origin: top;
  -webkit-transition: -webkit-transform 400ms ease-in-out;
  transition: -webkit-transform 400ms ease-in-out;
  transition: transform 400ms ease-in-out;
  transition: transform 400ms ease-in-out, -webkit-transform 400ms ease-in-out;
}

nav ul {
  margin: 0;
  padding: 0; 
  list-style: none;
}

nav li {
  margin-bottom: 1em; 
  margin-left: 1em;
}

nav a {
  color: var(--foreground-clr); 
  text-decoration: none;
  font-size: 1.2em;
  text-transform: uppercase;
  opacity: 0;
  -webkit-transition: opacity 150ms ease-in-out;
  transition: opacity 150ms ease-in-out;
}

nav a:hover {
  color: var(--accent-clr);
}

.nav-toggle:checked ~ nav {
  -webkit-transform: scale(1, 1);
          transform: scale(1, 1);
}

.nav-toggle:checked ~ nav a {
  opacity: 1;
  -webkit-transition: opacity 250ms ease-in-out 250ms;
  transition: opacity 250ms ease-in-out 250ms;
}

@media screen and (min-width: 500px){
    .nav-toggle-label::before {
    border: 1px solid var(--dark-accent-clr);
  }
}

@media screen and (min-width: 900px){

  .nav-toggle-label {
    display: none;
  }

  header {
    display: grid; 
    grid-template-columns: 1fr auto minmax(700px, 3fr) 1fr;
  }

  .logo {
    grid-column: 2 / 3;
  }

  nav {
    all: unset;
    grid-column: 3 / 4;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    -webkit-align-items: center;
            align-items: center;
  }

  nav ul {
    display: -webkit-flex;
    display: flex;
  }

  nav li {
    margin-left: 3em;
    margin-bottom: 0;
  }

  nav a {
    opacity: 1;
    position: relative;
  }

  nav a:before {
    content: "";
    display: block;
    height: .2em;
    background-color: var(--dark-accent-clr);
    position: absolute;
    border-radius: .1em;
    top: -.7em; 
    left: 0;
    right: 0;
    -webkit-transform: scale(0,1);
            transform: scale(0,1);
    -webkit-transform-origin: center;
            transform-origin: center;
    -webkit-transition: -webkit-transform ease-in-out 250ms;
    transition: -webkit-transform ease-in-out 250ms;
    transition: transform ease-in-out 250ms;
    transition: transform ease-in-out 250ms, -webkit-transform ease-in-out 250ms
  }

  nav a:hover::before {
    -webkit-transform: scale(1,1);
            transform: scale(1,1)
  }

}

