body{
  margin: 0;
  padding: 0;
  background-color: #222;
  --dark-accent-clr: #111;
  --background-clr: rgba(1, 80, 48, 1);
  --foreground: rgba(26, 29, 28, 0.85);
  --link-clr: lightblue;
  --text-clr: rgb(227, 226, 226);
  --accent-clr: goldenrod;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  font-size: clamp(16px, 1vw, 22px);
}

*, 
*::before, 
*::after {
 box-sizing: border-box;
}

/* ============ELEMENTS============ */
h1 {
  margin: 0;
  padding: 1em;
  font-size: 2em;
  text-transform: uppercase;
  color: var(--text-clr);
}

h2 {
  margin: 0;
  padding: 1em;
  font-size: 1.8em;
  text-transform: uppercase;
  color: var(--text-clr);
}

h3 {
  margin: 0;
  padding: 1em;
  font-size: 1.5em;
  text-transform: uppercase;
  color: var(--text-clr);
}

.button {
  padding: 1em;
  text-transform: uppercase;
  text-decoration: none;
  display: inline-block;
  border: 1px solid var(--link-clr);
  color: var(--text-clr);
  background-color: #111;
  padding: 1em;
  font-size: 1em;
  font-family: inherit;
  margin: .5em .7em;
  -webkit-transition:  ease-out 200ms;
  transition:  ease-out 200ms;
}

.button:hover {
  cursor: pointer;
  color: var(--accent-clr);
  border: 1px solid var(--text-clr);
  border-radius: .4em;
}


/* ====CONTAINERS ===== */

.content {
  min-height: 90vh;
  position: relative;
  top: 10vh;
  background-blend-mode: multiply;
  background-size: cover;
}

.container{
  margin: auto;
  width: 95%;
  text-align: center;
}

.card {
  border: 2px solid var(--text-clr);
  margin: 1em;
  font-weight: bold;
  padding: 1em;
}

.card span{
  color: var(--link-clr);
  font-size: 1.5em;
}

.card p {
  color: var(--text-clr)
}

.card-content {
  border-top: 3px solid var(--accent-clr);
  text-transform: capitalize;
  padding: .7em;
  margin: 1em auto;
  color: var(--text-clr);
}

/* =============Form Styles========== */
.form-container {
  border: 1px solid var(--text-clr);
  margin: 2em auto;
  padding: 1em;
}

.form{
  color: var(--text-clr);
}

.form  label, .form > input {
  display: block;
  margin: 0;
  padding: .6em;
}

.form input[type=radio] {
  display: none;
}

.form input[type=radio]:checked ~ label{
  width: 90%;
  border: 5px solid green;
  margin: 0 auto;
  color: var(--accent-clr)
}

 input, textarea, select{ 
  padding: 1.5em;
  width: 90%;
  font-size: 1em;
}

input::-webkit-calendar-picker-indicator{
  font-size: 1.5em;
}

input[type=date], input[type=time]{
letter-spacing: .2em;
text-align: center;;
}

 input:focus, textarea:focus { 
  outline: none;
  box-shadow: inset 0 0 1em var(--accent-clr);
}

/* ========FONT AWESOME STYLES======= */
.fa-arrow-alt-circle-left{
  color: var(--accent-clr);
  font-size: 2em;
}
.fa-arrow-alt-circle-right{
  color: var(--accent-clr);
   font-size: 2em;
}

.fa-trash{
  color: var(--accent-clr);
  font-size: .7em;
  -webkit-transition: ease-out 200ms;
  transition: ease-out 200ms;
}

.fa-trash:hover {
  color: var(--link-clr);
  font-size: .9em;
  cursor: pointer;
}

.fa-map-marker-alt {
    color: var(--accent-clr);
  font-size: 2em;
  margin: 0 1em;
  -webkit-transition: ease-out 200ms;
  transition: ease-out 200ms;
}
.fa-map-marker-alt:hover {
  color: var(--link-clr);
  font-size: 2.2em;
  cursor: pointer;
}

/* ===========MEDIA QUERIES========== */

@media screen and (min-width: 600px){
  header {
    height: 7vh;
  }
  .content {
    min-height: 93vh;
    top: 7vh
  }
}
